import * as React from 'react';
import { Routes, Route } from 'react-router-dom';
import { StaffLink } from '../components';

import './views.scss';

import StaffView from '../Views';

const GeneralSettingUpdateView = React.lazy(() => import('./views.GeneralUpdate'));
const IndexSettingUpdateView = React.lazy(() => import('./views.IndexUpdate'));
const PagesSettingUpdateView = React.lazy(() => import('./views.PagesUpdate'));

export default function SettingLayout() {
  return (
    <Routes>
      <Route path="pages/" element={<PagesSettingUpdateView />} />
      <Route path="general/" element={<GeneralSettingUpdateView />} />
      <Route path="index/" element={<IndexSettingUpdateView />} />

      <Route
        path="/"
        element={
          <StaffView title="Settings" back="/staff/" requiredPerms={['core.view_sitesetting']}>
            <div className="d-grid grid-sm-3 grid-md-4">
              <SettingLink to="general/" label="General" text="Site settings and branding" />
              <SettingLink to="index/" label="Index" text="Homepage settings" />
              <SettingLink to="pages/" label="Pages" text="Site pages settings" />
            </div>
          </StaffView>
        }
      />
    </Routes>
  );
}

const SettingLink = ({ label, text, ...rest }: any) => {
  return (
    <StaffLink {...rest} className="d-block">
      <span className="text-md">{label}</span>
      <p className="text-muted text-sm pb-3">{text}</p>
    </StaffLink>
  );
};
